import { Component, OnInit } from '@angular/core';
import { Renderer2 } from '@angular/core';
import axios from 'axios';//
@Component({
  selector: 'app-qifu',
  templateUrl: './qifu.component.html',
  styleUrls: ['./qifu.component.css']
})
export class QifuComponent implements OnInit {

  constructor(private render2: Renderer2) { }
  ngOnInit() {
    this.mounted();
  }
  public chunks = []
  public chunkList = {
    duration: 0,
    stream: ''
  }
  public btnText = ''
  ishide = true
  recorder: any
  audio: any
  blobEvent: any
  text: any = '123'
  requestAudioAccess() {
    navigator.mediaDevices.getUserMedia({ audio: true }).then(stream => {
      this.recorder = new (<any>window).MediaRecorder(stream);
      this.bindEvents();
    }, error => {
      alert('');
    });
  }

  timeOutEvent = 0;//   


  //   
  gtouchstart() {
    document.getElementById('btntext').innerHTML = '';
    this.timeOutEvent = window.setTimeout(() => {//
      this.timeOutEvent = 0;
      //   
      console.log("");
      this.onStart();
    },
      500);//500500   
    document.addEventListener('contextmenu', function (e) {//
      e.preventDefault();
    });

  };
  //500onclick   
  gtouchend() {
    document.getElementById('btntext').innerHTML = '';
    this.onStop();
    clearTimeout(this.timeOutEvent);//  
    if (this.timeOutEvent != 0) {
      //onclick   
      console.log("");
    }
    this.ishide = false
  };
  //   
  gtouchmove() {
    clearTimeout(this.timeOutEvent);//   
  };


  onStart() {
    this.recorder.start();
  }

  onStop() {
    this.recorder.stop();
  }

  play() {
    console.log(this.chunkList)
    const audio = this.render2.selectRootElement("#audio");
    audio.src = this.chunkList.stream;
    audio.play();
    var x = document.getElementsByClassName("span");
    for (let i = 0; i < 3; i++) {
      (<HTMLElement>x[i]).style.animation = "wink " + 1 + "s ease infinite";
      window.setTimeout(() => { (<HTMLElement>x[i]).style.animationPlayState = "running" }, 0);
      window.setTimeout(() => { (<HTMLElement>x[i]).style.animationPlayState = "paused" }, this.chunkList.duration * 1000 - 1);
    }
  }

  bindEvents() {

    this.recorder.ondataavailable = (e) => {
      this.chunks.push(e.data);
      console.log(e)
      console.log(this.chunks)
    };

    this.recorder.onstop = () => {
      console.log(this.chunks)
      //let blob = new Blob(this.chunks, { 'type': 'audio/ogg; codecs=opus' }),
      let blob = new Blob(this.chunks, { 'type': 'audio; codecs=opus' }),
        //let blob = new Blob(this.chunks, { 'type': 'audio/mpeg; codecs=opus' }),
        audioStream = URL.createObjectURL(blob),
        //
        duration = Math.round(blob.size / 6600);
      console.log(blob)
      if (duration <= 0) {
        //alert('');
        return;
      }
      if (duration > 60) {
        duration = 60;
      }
      this.chunkList = { duration: duration, stream: audioStream };
      this.chunks = [];
      document.getElementById('voiceposition').style.width = (this.chunkList.duration * 20 + 30) + 'px';//div
      document.getElementById('time').innerHTML = String(this.chunkList.duration) + '"';//
      document.getElementById('voice').style.width = this.chunkList.duration * 20 + 'px';//
      console.log(this.chunkList);
      //
      const formData = new FormData()
      formData.append('file', blob, 'voice.mp3')
      console.log(formData);
      var that = this
      axios({
        method: 'post',
        // url: '/api/gongfo/qifu',
        url: 'http://buddhism.cst.ifeel.vip:9000/gongfo/qifu',
        data: formData,
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
        .then(
          res => {
            console.log('')
            console.log(res.data)
            that.text = res.data
          }
        )
        .catch(
          err => {
            console.log('')
            that.text = ""
          }
        )
    };
  }
  mounted() {
    if (!navigator.mediaDevices) {
      alert('');
      return;
    }
    if (!(<any>window).MediaRecorder) {

      alert('');
      return;
    }
    this.audio = this.render2.selectRootElement("#audio");
    this.requestAudioAccess();
  }
}