import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';

export interface Message{
  Uid?:number,
  WechatID:string,
  Createdat?:string,
  Xinde:string,
  Title:string
}

@Component({
  selector: 'app-xinde',
  templateUrl: './xinde.component.html',
  styleUrls: ['./xinde.component.css']
})


export class XindeComponent implements OnInit {

  public message:string;
  public messagelist:Message[]=[];
  totalUser:number;
  public email:string;
  public tableList = [];
  public tablePageList = [];  //
  public pageNo = 1; //
  public preShow = false; //
  public nextShow = false; //
  public pageSize = 5; //
  public curPage = 1; //
  public pageNos:number[]=[5,10];
  constructor( public http:HttpClient ) {
   }

  ngOnInit() {
    this.getxinde();
  }
  
  getxinde(){
    let api = '/api/xiuxing/xinde';
    this.http.get<Message[]>(api).subscribe(res=>{
      console.log(res);
      this.addxinde(res);
      this.getPageList();
      this.onChangePageSize(3);
    })
  }

  addxinde(message:Message[]){
    this.messagelist=message;
  }


  getPageList() {
    if (this.messagelist.length >= 1) {//
      if (this.messagelist.length % this.pageSize === 0) {//10/5=29%5=1
        this.pageNo = Math.floor(this.messagelist.length / this.pageSize);//Math.floor
      } else {
        this.pageNo = Math.floor(this.messagelist.length / this.pageSize) + 1;
      }
      if (this.pageNo < this.curPage) {
        this.curPage = this.curPage - 1;
      }
      if (this.pageNo === 1 || this.curPage === this.pageNo) {
        this.preShow = this.curPage !== 1;
        this.nextShow = false;
      } else {
        this.preShow = this.curPage !== 1;
        this.nextShow = true;
      }
    } else {
      this.messagelist.length = 0;
      this.pageNo = 1;//1
      this.curPage = 1;//1
    }
    this.tablePageList = this.messagelist.slice((this.curPage - 1) * this.pageSize, (this.curPage) * this.pageSize)
  }
  //
  showPrePage() {
    this.curPage--;
    if (this.curPage >= 1) {
      this.getPageList();
    } else {
      this.curPage = 1;
    }
  }
  //
  showNextPage() {
    this.curPage++;
    if (this.curPage <= this.pageNo) {
      this.getPageList();
    } else {
      this.curPage = this.pageNo;
    }
  }
  //
  onChangePageSize(value) {
    this.pageSize = value;
    this.curPage = 1;
    this.getPageList();
  }
  
}