import { Injectable } from '@angular/core';
import { Audio } from '../interface/audio.model';
import { PlayData } from '../interface/playdata.model';
@Injectable({
  providedIn: 'root'
})
export class AudioService {

  private _audio: HTMLAudioElement;
  public playList: Audio[];
  //
  public playData: PlayData;
  private listenInterval;
  constructor() {
    //
    this._audio = document.createElement("audio");
    this._audio.autoplay = false;
    this._audio.onplay = () => {
      let that = this;
      //window setInterval() 
      this.listenInterval = window.setInterval(() => {
        that.playData.Current = that._audio.currentTime;//
        that.playData.Url = that._audio.src;//
        that.playData.During = that._audio.duration;//
        that.playData.Data = that._audio.buffered &&//
          that._audio.buffered.length ?
          (that._audio.buffered.end(0) || 0) : 0;
      }, 1000);
      this.playData.IsPlaying = true;
    };
    //
    this._audio.onended = () => {
      window.clearInterval(this.listenInterval);
      this.playData.Current = this._audio.currentTime;
      this.playData.Url = this._audio.src;
      this.playData.During = this._audio.duration;
      this.playData.Data = this._audio.buffered &&
        this._audio.buffered.length ?
        (this._audio.buffered.end(0) || 0) : 0;
      this.playData.IsPlaying = false;
    }
    //
    this._audio.onpause = () => {
      window.clearInterval(this.listenInterval);//
      this.playData.Current = this._audio.currentTime;
      this.playData.Url = this._audio.src;
      this.playData.During = this._audio.duration;
      this.playData.Data = this._audio.buffered &&
        this._audio.buffered.length ?
        (this._audio.buffered.end(0) || 0) : 0;
      this.playData.IsPlaying = false;

    };
    this.playData = { Style: 0, Index: 0 };
    this.playList = [
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E8%88%AC%E8%8B%A5%E6%B3%A2%E7%BD%97%E8%9C%9C%E5%A4%9A%E5%BF%83%E7%BB%8F_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%A4%A7%E6%82%B2%E5%92%92_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%8D%97%E6%97%A0%E9%98%BF%E5%BC%A5%E9%99%80%E4%BD%9B_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%85%AD%E5%AD%97%E7%9C%9F%E8%A8%80%E9%A2%82_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E6%B6%88%E7%81%BE%E5%90%89%E7%A5%A5%E7%A5%9E%E5%92%92_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E8%8E%B2%E8%8A%B1%E7%94%9F%E5%A4%A7%E5%A3%AB%E5%BF%83%E5%92%92_01.mp3', Title: '' },

      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E8%88%AC%E8%8B%A5%E6%B3%A2%E7%BD%97%E8%9C%9C%E5%A4%9A%E5%BF%83%E7%BB%8F_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%A4%A7%E6%82%B2%E5%92%92_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%8D%97%E6%97%A0%E9%98%BF%E5%BC%A5%E9%99%80%E4%BD%9B_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E5%85%AD%E5%AD%97%E7%9C%9F%E8%A8%80%E9%A2%82_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E6%B6%88%E7%81%BE%E5%90%89%E7%A5%A5%E7%A5%9E%E5%92%92_01.mp3', Title: '' },
      { Url: 'https://cst-1256261760.cos.ap-guangzhou.myqcloud.com/audio/%E8%8E%B2%E8%8A%B1%E7%94%9F%E5%A4%A7%E5%A3%AB%E5%BF%83%E5%92%92_01.mp3', Title: '' },
    ];
  }
  //
  public add(audio: Audio): void {
    this.playList.push(audio);
    if (this.playList.length === 1) {
      this.PlayIndex(0);
    }
  }
  //
  public Skip(percent: number): void {
    this._audio.currentTime = this._audio.duration * percent;
    this.playData.Current = this._audio.currentTime;
  }

  //
  public Prev(): void {
    switch (this.playData.Style) {
      case 0:
        if (this.playData.Index > 0) {
          this.playData.Index--;
          this.PlayIndex(this.playData.Index);
        }
        break;
      case 1:
        this.playData.Index = (this.playData.Index - 1) < 0 ?
          (this.playList.length - 1) :
          (this.playData.Index - 1);
        this.PlayIndex(this.playData.Index);
        break;
      case 2:
        this.playData.Index = (this.playData.Index - 1) < 0 ?
          (this.playList.length - 1) :
          (this.playData.Index - 1);
        this.PlayIndex(this.playData.Index);
        console.log('');
        break;
      case 3:
        this._audio.currentTime = 0;
        break;
      default:
        if (this.playData.Index > 0) {
          this.playData.Index--;
          this.PlayIndex(this.playData.Index);
        }
        break;
    }
  }
  public Toggle(audio?: Audio): void {
    let tryGet = audio ?
      this.playList.findIndex((p) => p.Url === audio.Url) :
      this.playData.Index;
    if (tryGet < 0) {
      this.playList.push(audio);
      this.PlayIndex(this.playList.length);
    } else {
      if (tryGet === this.playData.Index) {
        if (this._audio.paused) {
          this._audio.play();
          this.playData.IsPlaying = true;
        } else {
          this._audio.pause();
          this.playData.IsPlaying = false;
        }
      } else {
        this.PlayIndex(tryGet);
      }
    }
  }

  //
  public Next(): void {
    switch (this.playData.Style) {
      case 0:
        if (this.playData.Index < this.playList.length) {
          this.playData.Index++;
          this.PlayIndex(this.playData.Index);
        }
        break;
      case 1:
        this.playData.Index = (this.playData.Index + 1) % this.playList.length;
        this.PlayIndex(this.playData.Index);
        break;
      case 2:
        this.playData.Index = (this.playData.Index + 1) % this.playList.length;
        this.PlayIndex(this.playData.Index);
        console.log('');
        break;
      case 3:
        this._audio.currentTime = 0;
        break;
      default:
        if (this.playData.Index < this.playList.length) {
          this.playData.Index++;
          this.PlayIndex(this.playData.Index);
        }
        break;
    }
  }
  //
  public PlayIndex(index: number): void {
    index = this.playList[index] ? index :
      this.playList[index + 1] ? (index + 1) :
        this.playList[index - 1] ? (index - 1) : -1;
    if (index !== -1) {
      this._audio.src = this.playList[index].Url;
      if (this._audio.paused) {
        this._audio.play();
        this.playData.IsPlaying = true;
      }
      this.playData.Index = index;
    } else {
      console.log("nothing to be played.");
    }
  }

  public PlayList(): Audio[] {
    return this.playList;
  }

  public PlayData(): PlayData {
    return this.playData;
  }
}

